<template>
  <v-app>
    <NavbarMenu />
    <div class="content">
      <v-container style="margin-top: 30px" class="mb-12">
        <v-card
          v-for="(ques, index) in questions"
          :key="ques.questionId"
          class="ques_item"
        >
          <v-container fluid>
            <p>
              <span>{{ index + 1 }}.[{{ ques.diseaseName }}]</span>
              {{ ques.questionInfo }}({{ ques.score }} 分)
            </p>
            <!-- <v-radio-group v-model="choice"> -->
            <!-- <v-radio-group> -->
            <v-radio-group v-model="rs[index].value">
              <v-radio :label="ques.choiceA" color="red" value="1"></v-radio>
              <!-- @change="changeResult(ques.questionId,value)" -->
              <v-radio :label="ques.choiceB" color="red" value="2"></v-radio>
              <v-radio :label="ques.choiceC" color="red" value="3"></v-radio>
              <v-radio :label="ques.choiceD" color="red" value="4"></v-radio>
            </v-radio-group>
          </v-container>
        </v-card>
        <div class="text-center">
          <v-dialog v-model="dialog" width="500">
            <template v-slot:activator="{ on }">
              <v-btn color="red lighten-2" dark v-on="on" @click="getscore" style="margin-top:20px">
                查看分数
              </v-btn>
            </template>
            <v-card> 
              <v-container>
                分数： {{score}}/{{sumscore}}
              </v-container>
            </v-card>
          </v-dialog>
          <!-- <v-btn
            rounded
            color="primary"
            dark
            @click="getscore()"
            style="margin-top: 20px"
          >
            查看分数
          </v-btn> -->
          <!-- <v-btn
            rounded
            color="primary"
            dark
            @click="use()"
            style="margin-top: 20px"
          >
            刷新
          </v-btn> -->
        </div>
      </v-container>
    </div>
  </v-app>
</template>

<script>
import axios from "axios";
import qs from "qs";
// import Vue from 'vue'
export default {
  name: "ExamItem",
  // inject:['reload'],
  components: {
    NavbarMenu: () => import("../components/NavbarMenu"),
  },
  data() {
    return {
      score:0,
      sumscore:0,
      dialog:false,
      choice: [],
      // rsesults: [],
      questions: [
        {
          questionId: "011",
          questionInfo: "题干1",
          diseaseName: "病种名1",
          score: "10",
          // choice: [{ choice: "aaa1",value: "A" },{ choice: "",value: 1 },{ choice: "",value: 1 },{ choice: "",value: 1 },],
          choiceA: "aaa1",
          choiceB: "bbb1",
          choiceC: "ccc1",
          choiceD: "ddd1",
        },
        // {
        //   questionId: "012",
        //   questionInfo: "题干2",
        //   diseaseName: "病种名2",
        //   score: "10",
        //   choiceA: "aaa2",
        //   choiceB: "bbb2",
        //   choiceC: "ccc2",
        //   choiceD: "ddd2",
        // },
      ],
      rs: [
        {
          questionId: "",
          value: "000",
        },
      ],
    };
  },
  mounted() {
    this.updata();
  },
  methods: {
    updata() {
      axios
        .post("/test/paper", qs.stringify({ testId: this.$route.query.id }))
        .then((res) => {
          if (res.data.code === 200) {
            this.questions = res.data.data;
            this.rs = [];
            this.questions.forEach((item) => {
              this.rs.push({
                questionId: item.questionId,
                value: "",
              });
            });
            console.log("axios中的questions", this.questions);
            console.log("res.data.data", res.data.data);
          } 
          else if (res.data.code === -1) {
            window.sessionStorage.clear(); // 清空token
            var href = res.data.data.domain;
            window.location.href = href;
          }
          else {
            console.log("错误代码：", res.data.code);
          }
        })
        .catch((err) => {
          console.log("err", err.data);
        });
    },
    getscore() {
      // this.score = 100;

      console.log("查看分数后的rs:", this.rs);
      // var i = this.rs[0].value;
      // this.$store.dispatch("snackbar/openSnackbar", {
      //   msg: i,
      //   color: "primary",
      // });
      axios
        .post(
          "/test/score",
          qs.stringify({
            result: this.rs,
            testId:this.$route.query.id,
          })
        )
        .then((res) => {
          // console.log("res:", res);
          if (res.data.code === 200) {
            this.score = res.data.data.score
            this.sumscore = res.data.data.sumscore
            console.log("res:", res)
            // this.$store.dispatch("snackbar/openSnackbar", {
            //   msg: `您的分数为:${res.data.data.score}/${res.data.data.sumscore}`,
            //   color: "primary",
            // });
          } 
          else if (res.data.code === -1) {
            window.sessionStorage.clear(); // 清空token
            var href = res.data.data.domain;
            window.location.href = href;
          }
          else {
            console.log("错误代码：", res.data.code);
          }
        })
        .catch((err) => {
          console.log("err=>", err);
        });
      // console.log(this.questions[index]);
      // console.log(this.choice);
    },
  },
};
</script>


